<template>
  <div class="row">
    <div class="col-md-12 pb-2 p-0 bg-platinum">
      <h1 id="portfolio" class="text-center bg-darkcyan text-platinum py-3 border-bottom">Portfolio</h1>
    </div>
    <div class="col-md-12 d-flex justify-content-center flex-wrap bg-platinum">
      <div
        class="card float-left border border-darkcyan m-2 shadow"
        v-for="(item, index) in githubArray"
        :key="index"
        style="width:18rem"
      >
        <div class="card-body" v-if="item.homepage != 'https://mrrwmix.github.io'">
          <h5 class="card-title text-darkteal">{{item.name}}</h5>
          <p class="card-text">{{item.description}}</p>
        </div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            {{item.language}}
            <i
              class="fab fa-react text-info"
              v-if="item.description.toLowerCase().includes('react')"
            />
            <i
              class="fab fa-vuejs text-darkcyan"
              v-else-if="item.language.toLowerCase() === 'vue' || item.description.toLowerCase().includes('vue')"
            ></i>
            <i
              class="fab fa-node text-success"
              v-else-if="item.description.toLowerCase().includes('node')"
            ></i>
            <i
              class="fab fa-js-square text-warning"
              v-else-if="item.language.toLowerCase() === 'javascript'"
            />
            <i class="fab fa-java" v-else-if="item.language.toLowerCase()==='java'" />
            <i class="fab fa-css3-alt text-primary" v-else-if="item.language.toLowerCase()==='css'"></i>
            <i class="fab fa-html5 text-danger" v-else />
          </li>
        </ul>
        <div class="card-body">
          <a
            :href="item.homepage"
            v-if="item.homepage != null && item.homepage != ''"
            class="card-link text-info"
            target="_blank"
          >Deployed URL</a>
          <a :href="item.html_url" class="card-link text-info" target="_blank">Source Code</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      githubArray: []
    };
  },
  mounted() {
    axios
      .get(
        "https://api.github.com/users/Mrrwmix/repos?sort=updated&per_page=100"
      )
      .then(resp => {
        this.githubArray = resp.data;
      });
  }
};
</script>